.quizHeader {
  grid-column: full-start/full-end;

  display: grid;
  grid-template-columns: repeat(5, minmax(min-content, 1fr));

  &__left {
    grid-column: 1/3;
    background-color: #000;
    color: #fff;

    display: grid;
    grid-template-rows: repeat(5, min-content);
    // grid-template-columns: max-content;
    grid-auto-flow: row;
    row-gap: 2rem;
    padding: 5rem 10rem;

    @media only screen and (max-width: 990px) {
      grid-column: 1/-1;
      justify-items: center;
      padding: 15rem 10rem;

      @media only screen and (max-width: $bp-phone) {
        padding: 8rem 2rem;
        justify-content: center;
      }
    }

    img {
      padding-bottom: 3rem;
    }
  }

  &__title {
    font-size: 7rem;
    line-height: 1;

    @media only screen and (max-width: 990px) {
      width: 80%;
      text-align: center;
    }

    @media only screen and (max-width: $bp-phone) {
      width: 100%;
    }

    @media only screen and (max-width: 350px) {
      font-size: 5rem;
    }
  }

  &__text {
    padding-top: 2rem;
    padding-bottom: 3rem;
    font-size: 1.8rem;

    @media only screen and (max-width: $bp-phone) {
      font-size: 2rem;
      text-align: center;
    }
  }

  &__btn {
    justify-self: start;
    padding: 1.5rem 3rem;
    border: none;
    letter-spacing: 0.5px;
    font-size: 1.5rem;
    line-height: 1.5;
    border-radius: 5rem;
    background-color: #ffad8b;
    transition: background-color 0.2s;

    @media only screen and (max-width: 990px) {
      justify-self: center;
    }

    &:hover {
      background-color: #ffd4c2;
    }
  }

  &__small {
    color: gray;
    font-size: 1.3rem;
    letter-spacing: 0.8px;

    @media only screen and (max-width: $bp-phone) {
      font-size: 1.5rem;
      text-align: center;
    }
  }

  &__right {
    grid-column: 3/6;
    background-color: #ffad8b;
    text-align: center;
    padding: 5rem 10rem;
    img {
      width: 100%;
    }

    @media only screen and (max-width: 990px) {
      display: none;
    }
  }
}
