.quizAbout {
  grid-column: center-start/center-end;
  padding-bottom: 10rem;

  @media only screen and (max-width: $bp-phone) {
    grid-column: full-start/full-end;
    padding: 0 2rem;
  }

  &__title {
    font-size: 6rem;
    margin: 5rem auto 5rem auto;
    width: 80%;
    text-align: center;
    line-height: 1;

    @media only screen and (max-width: $bp-phone) {
      width: 100%;
      font-size: 5rem;
      margin: 0;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, min-content);
    gap: 5rem;
    justify-items: center;

    @media only screen and (max-width: 600px) {
      gap: 2rem;
    }

    img {
      grid-column: 1/-1;
      width: 90%;
      padding: 2rem 1rem;
    }

    p {
      text-align: left;
      font-size: 2rem;
    }
  }

  button {
    display: block;
    margin: 4rem auto 6rem;
    color: #fff;
    background-color: #000;
    border-radius: 5rem;
    padding: 1.5rem 3rem;
    border: none;
    outline: none;
    line-height: 1.5;
    font-size: 1.5rem;
    font-weight: 500;
    cursor: pointer;
    transition: 0.2s;

    &:hover {
      background-color: #333;
    }
  }

  &__p {
    @media only screen and (max-width: 600px) {
      grid-column: 1/-1;
      text-align: center !important;
    }
  }

  small {
    width: 100%;
    text-align: center;
    font-size: 16px;
    padding: 0 80px;
    line-height: 1.5;
    display: block;
    letter-spacing: 1.2px;

    @media only screen and (max-width: 600px) {
      padding: 0;
      padding-bottom: 3rem;
    }
  }
}
