.header {
  grid-column: full-start / full-end;
  background-image: url(../images/main-header.jpg);
  padding: 20rem 0;
  // margin-top: 2rem;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50%;

  @media only screen and (max-width: $bp-mid) {
    padding: 10rem 0;
    background-image: none;
    background-color: #fcd0ba;
  }

  @media only screen and (max-width: $bp-phone) {
    padding-bottom: 0;
  }

  &__text {
    max-width: 33.3333%;

    @media only screen and (max-width: $bp-large) {
      max-width: 30%;
    }
    @media only screen and (max-width: $bp-mid) {
      display: grid;
      // grid-auto-flow: row;
      row-gap: 2rem;
      justify-items: center;
      max-width: 100%;
    }

    h1,
    p {
      @media only screen and (max-width: $bp-mid) {
        text-align: center;
      }
    }
    h1 {
      @media only screen and (max-width: $bp-mid) {
        font-size: 50px;
        font-weight: 900;
      }
    }

    p {
      @media only screen and (max-width: $bp-mid) {
        font-size: 16px;
      }
    }
    img {
      @media only screen and (max-width: $bp-mid) {
        display: inline-block;
      }
      @media only screen and (max-width: $bp-phone) {
        width: 80%;
        padding: 2rem 4rem 4rem;
      }
    }
  }

  &__small {
    display: block;
    font-size: 1.6rem;
    font-style: italic;
    font-weight: 400;
    margin-top: 1rem;
    letter-spacing: 1px;

    @media only screen and (max-width: $bp-mid) {
      display: none;
    }
  }

  &__icons {
    margin-top: 4rem;
    display: flex;

    @media only screen and (max-width: $bp-mid) {
      display: none;
    }

    img {
      width: 20rem;
      margin-right: 2rem;
      cursor: pointer;

      @media only screen and (max-width: 1000px) {
        width: 15rem;
      }

      @media only screen and (max-width: 800px) {
        width: 12rem;
      }
    }
  }
}

.content-container {
  // width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin: 0 5rem;

  @media only screen and (max-width: $bp-phone) {
    margin: 0;
  }
}
