.card1 {
  grid-column: 1/-1;
  height: 60rem;

  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 3rem;

  background-image: url(../images//home-tile-1.jpg);
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: top;

  border-radius: 12px;
  padding: 4rem;
  margin: 2rem;

  @media only screen and (max-width: $bp-mid) {
    background-image: url(../images//home-tile-mobile.jpg);
    background-position-y: bottom;
  }

  @media only screen and (max-width: $bp-phone) {
    padding: 1.5rem;
    padding-top: 5rem;
  }

  &__title {
    grid-column: 1/4;
    grid-row: 1/4;
    justify-self: start;
    font-size: 5rem;
    line-height: 1.2;
    padding: 2rem;
    padding-left: 0;
    align-self: center;
    transform: translateY(-9.5rem);

    @media only screen and (max-width: 800px) {
      transform: translateY(-5.5rem);
    }

    @media only screen and (max-width: $bp-mid) {
      grid-column: 1/-1;
      grid-row: 1/2;
    }

    @media only screen and (max-width: $bp-phone) {
      grid-column: 1 / span 5;
      grid-row: 1 / span 2;
      font-size: 3.5rem;
    }

    @media only screen and (max-width: 330px) {
      grid-column: 1 / 3;
      grid-row: 1/2;
    }

    &--back {
      grid-column: 1/5;
      justify-self: start;
      grid-row: 1/3;

      @media only screen and (max-width: $bp-phone) {
        grid-column: 1/-1;
        grid-row: 1/2;
        justify-self: center;
      }
    }
  }

  &__text {
    grid-column: 1/4;
    grid-row: 2/3;
    margin-top: -7rem;
    font-size: 2rem;

    @media only screen and (max-width: $bp-phone) {
      grid-column: 1/-1;
      line-height: 1.8;
    }
  }

  div {
    grid-row: 5/6;
  }

  &__btn {
    border-radius: 5rem;
    border: none;
    text-transform: uppercase;
    padding: 2rem 2rem 2rem 1rem;
    cursor: pointer;

    &::before {
      content: "";
      padding: 6.5px 23.00885px;
      display: inline;
      background: url(../images/crossIcon.svg) no-repeat 50%;
    }
  }

  &--back {
    background-image: none;
    background-color: #fcd0ba;
  }
}
