.footer {
  grid-column: full-start / full-end;
  background-color: var(--primary-color-black);

  display: grid;
  // grid-template-columns: 0.3fr repeat(4, max-content) 0.3fr;
  grid-template-columns: repeat(2, min-content) 0.1fr repeat(
      2,
      minmax(min-content, max-content)
    );
  grid-template-rows: repeat(8, min-content);
  justify-content: center;
  align-items: center;
  column-gap: 5rem;
  row-gap: 1rem;
  padding: 4rem 10rem;
  margin-bottom: 2rem;

  @media only screen and (max-width: $bp-mid) {
    grid-template-columns: none;
    grid-template-rows: none;
    grid-auto-flow: row;
    row-gap: 3rem;
    padding: 13rem;
  }

  @media only screen and (max-width: $bp-phone) {
    padding: 6rem 3rem 10rem 3rem;
  }

  &__logo {
    grid-column: 1/2;
  }

  &__link {
    font-size: 1.6rem;
    a {
      text-decoration: none;
      color: #fff;
      display: block;
      transition: 0.2s;
      pointer-events: none;

      &:hover {
        color: var(--primary-color-orange);
      }

      @media only screen and (max-width: $bp-mid) {
        font-size: 1.8rem;
        display: inline;
      }
    }

    &--1 {
    }
    &--2 {
      grid-column: 4/5;
      padding: 0 3rem 0 0;

      @media only screen and (max-width: $bp-mid) {
        grid-column: 1/2;
      }
    }
    &--3 {
    }
    &--text {
      grid-column: 1/2;
      width: 27rem;
      padding: 0 3rem 0 0;

      @media only screen and (max-width: $bp-mid) {
        grid-row: 2/3;
        width: 100%;
      }

      @media only screen and (max-width: $bp-phone) {
        padding: 0;
      }

      p {
        font-size: 1.2rem;
        color: #b3b3b3;

        @media only screen and (max-width: $bp-mid) {
          font-size: 1.4rem;
        }

        @media only screen and (max-width: $bp-phone) {
          font-size: 1.5rem;
        }
      }
    }
    &--4 {
      align-self: start;
    }
    &--5 {
      grid-column: 4/5;
      align-self: start;
      padding: 0 7rem 0 0;

      @media only screen and (max-width: $bp-mid) {
        grid-column: 1/2;
      }
    }
    &--6 {
      align-self: start;
    }
    &--hr {
      height: 1px;
      background-color: #4d4d4d;
      grid-column: 1/-1;
      margin: 4rem 0;
    }

    &--hide {
      @media only screen and (max-width: $bp-mid) {
        display: none !important;
      }
    }

    &--display {
      @media only screen and (max-width: $bp-mid) {
        display: inline;
      }
    }
  }

  &__bottom {
    &--1 {
      // grid-row: 4/5;
      grid-column: 1/-1;
    }
    &--2 {
      // grid-row: 5/6;
      grid-column: 1/-1;
    }
    &--3 {
      // grid-row: 6/7;
      grid-column: 1/-1;
    }
    &--4 {
      // grid-row: 7/8;
      grid-column: 1/-1;
    }
    &--5 {
      // grid-row: 8/9;
      grid-column: 1/-1;
    }

    p {
      font-size: 1.3rem;
      color: #b3b3b3;
    }
  }
}
