.nav-container {
  grid-column: full-start / full-end;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;

  &__scrolled {
    background-color: #fff;
    z-index: 10;
  }

  @media only screen and (max-width: $bp-mid) {
    background-color: #fff;
  }
}

.navbar {
  display: grid;
  grid-template-columns: repeat(2, minmax(min-content, 1fr));
  align-items: center;
  padding: 2rem 0;
  // margin: 0 5rem;
  z-index: 10;

  @media only screen and (max-width: $bp-large) {
    grid-template-columns: repeat(3, minmax(min-content, 1fr));
  }

  @media only screen and (max-width: $bp-large) {
    padding: 1rem 0;
  }
  @media only screen and (max-width: $bp-phone) {
    padding: 1rem 0.5rem;
  }

  &__logo {
    padding: 1rem 0.5rem;

    @media only screen and (max-width: $bp-phone) {
      width: 90%;
    }
  }

  &__ul {
    display: grid;
    grid-template-columns: repeat(4, minmax(min-content, 1fr));
    align-items: center;
    justify-items: end;
    list-style: none;

    @media only screen and (max-width: $bp-large) {
      display: none;
    }

    @media only screen and (max-width: 1250px) {
      justify-items: center;
    }
  }

  &__item {
    &--collapse:last-child {
      padding-bottom: 10px;
    }
    &--last {
      grid-column: 3/-1;

      @media only screen and (max-width: 1140px) {
        margin-left: -20px;
      }
    }
  }

  &__link {
    text-decoration: none;
    font-weight: 500;
    color: inherit;
    transition: 0.2s;

    &:hover {
      color: var(--color-hover-link);
    }

    &--disable {
      pointer-events: none;
    }
  }

  &__btn {
    &--hidden {
      cursor: pointer;
      padding: 0.375rem 0.75rem;
      font-size: 2rem;
      line-height: 1.5;
      border-radius: 50px;
      background-color: #000;
      color: #fff;
      border: 1px solid transparent;
      justify-self: center;

      @media only screen and (max-width: $bp-large) {
        display: block;
      }

      @media only screen and (max-width: $bp-phone) {
        font-size: 1.5rem;
      }
    }

    &--toggle {
      cursor: pointer;
      padding: 3rem 2rem;
      background-color: transparent;
      border: none;
      justify-self: end;

      @media only screen and (max-width: $bp-large) {
        display: block;
      }
    }

    &--icon {
      height: 2px;
      width: 4rem;
      background-color: #000;
      display: block;

      @media only screen and (max-width: $bp-phone) {
        width: 3rem;
      }

      &::before,
      &::after {
        content: "";
        height: 2px;
        width: 4rem;
        display: block;
        background-color: #000;

        @media only screen and (max-width: $bp-phone) {
          width: 3rem;
        }
      }

      &::before {
        transform: translateY(-1.1rem);
      }
      &::after {
        transform: translateY(0.9rem);
      }
    }
  }

  &__collapse {
    display: grid;
    grid-auto-flow: row;
    justify-items: center;
    list-style: none;
    row-gap: 1rem;

    &--hide {
      display: none;
    }
  }
}
