.jumboLarge {
  grid-column: full-start / full-end;
  background-color: var(--secondary-color-grey);

  // display: grid;
  // grid-template-columns: 0.3fr repeat(3, 1fr) 0.3fr;
  // grid-template-rows: repeat(3, 1fr);
  // justify-items: center;
  // align-items: start;
  padding: 8rem;
  // column-gap: 3rem;

  &__title {
    letter-spacing: 2px;
    text-transform: uppercase;
    font-size: 2rem;
    text-align: center;
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    align-items: center;

    @media only screen and (max-width: $bp-mid) {
      grid-template-columns: none;
      grid-auto-flow: row;
      row-gap: 2rem;
    }

    @media only screen and (max-width: $bp-phone) {
      row-gap: 5rem;
    }
  }

  &__img {
    width: 90%;
    padding: 2rem;

    @media only screen and (max-width: $bp-phone) {
      padding: 0;
      width: 100%;
    }

    &--1 {
    }

    &--2 {
    }

    &--3 {
    }

    &--4 {
      width: 80%;
    }

    &--5 {
      width: 100%;
    }

    &--6 {
      width: 95%;
    }
  }
}
