:root {
  --primary-color-orange: #ffad8b;
  --primary-color-black: #000000;
  --secondary-color-grey: #efefef;
  --color-hover-link: #ff9b72;
  --color-input-border: #ececec;

  --font-primary: "Archivo Narrow", sans-serif;
  --font-display: "Domine", sans-serif;
}

//RESPONSIVE BREAKPOINTS
$bp-largest: 90em; // 1440px
$bp-large: 68.75em; // 1100px
$bp-mid: 46.875em; // 750px
// $bp-small: 43.75em; // 700px
$bp-phone: 41.25em; // 660px

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;

  @media only screen and (max-width: $bp-largest) {
    font-size: 55%;
  }
}

body {
  font-family: var(--font-primary);
  color: var(--primary-color-black);
  font-size: 1.7rem;
  font-weight: 300;
  line-height: 1.6;
  text-rendering: optimizeLegibility;
  text-align: left;

  &::selection {
    background-color: var(--primary-color-orange);
  }

  &::-moz-selection {
    background-color: var(--primary-color-orange);
  }
}

.container {
  display: grid;

  grid-template-columns:
    [full-start] minmax(6rem, 1fr) [center-start] repeat(
      8,
      [col-start] minmax(min-content, 14rem) [col-end]
    )
    [center-end] minmax(6rem, 1fr) [full-end];

  // @media only screen and (max-width: $bp-phone) {
  //   grid-template-columns:
  //     [full-start] minmax(6rem, 1fr) [center-start] repeat(
  //       3,
  //       [col-start] minmax(min-content, 14rem) [col-end]
  //     )
  //     [center-end] minmax(6rem, 1fr) [full-end];
  // }
}
