.homeFeedOne {
  grid-column: center-start / center-end;
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-top: 8rem;

  display: grid;
  grid-template-columns: repeat(5, minmax(min-content, 1fr));
  grid-template-rows: min-content 1fr;
  row-gap: 3rem;

  @media only screen and (max-width: $bp-phone) {
    grid-column: full-start / full-end;
    margin-top: 4rem;
  }

  &__title {
    grid-column: 1/-1;
    justify-self: center;
    font-size: 2rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: -1rem;
    font-weight: 600;
    text-align: center;
  }
}
