.disclosure {
  display: flex;
  flex-wrap: wrap;
  align-content: center;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  z-index: 10;
  position: fixed;
  bottom: 0;
  left: 0;
  text-align: center;
  background-color: #fff;
  padding-top: 2rem;
  box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1);

  p {
    margin-top: 0;
    margin-bottom: 1rem;
    font-size: 1.4rem;
    font-weight: 600;
    letter-spacing: 1.2px;

    @media only screen and (max-width: $bp-phone) {
      padding: 0 2rem;
    }
  }
}
