%heading {
  font-family: var(--font-display);
  margin-bottom: 1rem;
  margin-top: 0rem;
}

.heading-1 {
  @extend %heading;
  font-size: 9rem;
  line-height: 1;
  font-weight: 900;

  @media only screen and (max-width: $bp-large) {
    font-size: 5rem;
  }
}

.heading-2 {
  @extend %heading;
  font-size: 2rem;
  font-weight: 400;
  line-height: 1.5;
  margin-bottom: 2rem;

  @media only screen and (max-width: $bp-large) {
    font-size: 1.1rem;
  }
}

.heading-3 {
  @extend %heading;
}

.heading-4 {
  @extend %heading;
}

.mb-sm {
  margin-bottom: 2rem;
}
.mb-md {
  margin-bottom: 3rem;
}
.mb-lg {
  margin-bottom: 4rem;
}
.mb-xl {
  margin-bottom: 8rem;
}

p::selection,
h1::selection,
h2::selection,
h3::selection,
h4::selection,
small::selection {
  background-color: var(--primary-color-orange);
}
p::-moz-selection,
h1::-moz-selection,
h2::-moz-selection,
h3::-moz-selection,
h4::-moz-selection,
small::selection {
  background-color: var(--primary-color-orange);
}

.btn {
  color: #fff;
  background-color: #000;
  outline: none;
  border: none;
  font-size: 1.5rem;
  user-select: none;
  text-align: center;
  white-space: nowrap;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
    border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out,
    -webkit-box-shadow 0.15s ease-in-out;

  &:hover {
    background-color: #8c918a;
  }
}
