.card2 {
  height: 60rem;

  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(5, 1fr);
  gap: 3rem;

  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  background-position-y: top;

  border-radius: 12px;
  padding: 4rem;
  margin: 2rem;

  @media only screen and (max-width: $bp-phone) {
    background-position: 50%;
    background-position-y: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: bottom;
  }

  &__title {
    grid-column: 1/-1;
    justify-self: center;
    font-size: 4.5rem;
    line-height: 1.2;
    padding: 2rem 0;
    align-self: center;
    transform: translateY(-35px);

    @media only screen and (max-width: $bp-mid) {
      font-size: 3.5rem;
    }

    @media only screen and (max-width: 400px) {
      grid-column: 1/3;
      grid-row: 1/2;
    }
  }

  &__text {
    grid-column: 1/-1;
    transform: translateY(-10rem);
    margin-top: 2.5rem;
    font-size: 2rem;

    @media only screen and (max-width: 340px) {
      grid-column: 1/3;
    }
  }

  &__div {
    grid-row: 5/6;

    &--back {
      grid-row: 3/4;
    }
  }

  &__btn {
    border-radius: 5rem;
    border: none;
    text-transform: uppercase;
    padding: 2rem 2rem 2rem 1rem;
    cursor: pointer;

    &::before {
      content: "";
      padding: 6.5px 23.00885px;
      display: inline;
      background: url(../images/crossIcon.svg) no-repeat 50%;
    }
  }
}
