.quizSection {
  grid-column: center-start/center-end;

  @media only screen and (max-width: $bp-phone) {
    grid-column: full-start/full-end;
    padding: 0 2rem;
  }

  &__title {
    font-size: 6rem;
    text-align: center;
    margin: 12rem auto 5rem auto;
    width: 80%;
    line-height: 1;

    @media only screen and (max-width: $bp-phone) {
      width: 100%;
      font-size: 5rem;
      margin-top: 6rem;
    }
  }

  &__persons {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(30rem, 1fr));
    column-gap: 5rem;

    @media only screen and (max-width: $bp-phone) {
      justify-content: center;
    }
  }

  &__unit {
    display: grid;
    grid-template-columns: 1fr;
    justify-items: center;

    img {
      width: 80%;
      align-self: end;
    }

    h2 {
      font-size: 2.8rem;
    }

    p {
      text-align: center;
      font-size: 1.75rem;
      padding: 0 4rem;
      width: 80%;

      @media only screen and (max-width: 800px) {
        font-size: 2rem;
      }
    }
  }

  button {
    display: block;
    margin: 4rem auto 6rem;
    color: #fff;
    background-color: #000;
    border-radius: 5rem;
    padding: 1.5rem 3rem;
    border: none;
    outline: none;
    line-height: 1.5;
    font-size: 1.5rem;
    font-weight: 500;
    cursor: pointer;
    transition: 0.2s;

    &:hover {
      background-color: #333;
    }
  }
}
