.doubleCards {
  grid-column: center-start / center-end;
  margin: 8rem 0;

  @media only screen and (max-width: $bp-phone) {
    grid-column: full-start / full-end;
    margin: 4rem 0;
  }

  // display: grid;
  // grid-template-columns: repeat(8, 1fr);
  // grid-template-rows: repeat(3, max-content);

  &__title {
    font-size: 2rem;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 1rem;
    font-weight: 600;
    text-align: center;
  }

  &__h2 {
    margin-bottom: 4rem;
    font-size: 5.5rem;
    line-height: 1.2;
    text-align: center;

    @media only screen and (max-width: $bp-phone) {
      font-size: 4rem;
      margin-bottom: 2rem;
    }
  }

  &__grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(50rem, 1fr));

    @media only screen and (max-width: 450px) {
      grid-template-columns: repeat(auto-fit, minmax(45rem, 1fr));
    }

    @media only screen and (max-width: 390px) {
      grid-template-columns: repeat(auto-fit, minmax(40rem, 1fr));
    }

    @media only screen and (max-width: 350px) {
      grid-template-columns: repeat(auto-fit, minmax(20rem, 1fr));
    }
  }

  &__black {
    background-image: url(../images/home-card-black.jpg);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: top;

    height: 70rem;
    margin: 2rem;
    padding: 4rem;
    border-radius: 1.2rem;
    text-align: center;

    @media only screen and (max-width: 985px) {
      height: 125rem;
    }

    @media only screen and (max-width: $bp-phone) {
      background-image: url(../images/home-card-black-mobile.jpg);
      height: 80rem;
    }

    @media only screen and (max-width: 350px) {
      background-image: url(../images/home-card-black-mobile.jpg);
      height: 60rem;
    }

    h2 {
      margin-bottom: 4rem;
      font-size: 5.5rem;
      line-height: 1.2;

      @media only screen and (max-width: 350px) {
        font-size: 4.5rem;
      }
    }
  }

  &__peach {
    background-image: url(../images/home-card-peach.jpg);
    background-position: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: top;
    color: #fff;
    height: 70rem;
    margin: 2rem;
    padding: 4rem;
    border-radius: 1.2rem;
    text-align: center;

    @media only screen and (max-width: 985px) {
      height: 125rem;
    }

    @media only screen and (max-width: $bp-phone) {
      background-image: url(../images/home-card-peach-mobile.jpg);
      height: 80rem;
    }

    @media only screen and (max-width: 350px) {
      background-image: url(../images/home-card-black-mobile.jpg);
      height: 60rem;
    }

    h2 {
      margin-bottom: 4rem;
      font-size: 5.5rem;
      line-height: 1.2;
      color: inherit;

      @media only screen and (max-width: 350px) {
        font-size: 4.5rem;
      }
    }
  }
}
